.basic-layout,
.basic-layout .antui-pageform {
  background: #03A9F4;
  background-image: -webkit-linear-gradient(30deg, #01579b 25%, #01579b 25.05%, transparent 0), -webkit-linear-gradient(100deg, #03a9f4 20%, #03a9f4 20.05%, transparent 0), -webkit-linear-gradient(325deg, transparent 70%, #039be5 70%, #039be5 75%), -webkit-linear-gradient(325deg, #0288d1 45%, #03a9f4 45.01%, transparent 0), -webkit-linear-gradient(325deg, #039be5 70%, #039be5 70.05%, transparent 0);
  background-image: -o-linear-gradient(30deg, #01579b 25%, #01579b 25.05%, transparent 0), -o-linear-gradient(100deg, #03a9f4 20%, #03a9f4 20.05%, transparent 0), -o-linear-gradient(325deg, transparent 70%, #039be5 70%, #039be5 75%), -o-linear-gradient(325deg, #0288d1 45%, #03a9f4 45.01%, transparent 0), -o-linear-gradient(325deg, #039be5 70%, #039be5 70.05%, transparent 0);
  background-image: linear-gradient(60deg, #01579b 25%, #01579b 25.05%, transparent 0), linear-gradient(350deg, #03a9f4 20%, #03a9f4 20.05%, transparent 0), linear-gradient(125deg, transparent 70%, #039be5 70%, #039be5 75%), linear-gradient(125deg, #0288d1 45%, #03a9f4 45.01%, transparent 0), linear-gradient(125deg, #039be5 70%, #039be5 70.05%, transparent 0);
}
.basic-layout nav {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}
.basic-layout nav a {
  margin: 6px;
}
.basic-layout .am-navbar {
  background: transparent;
  color: rgba(255, 255, 255, 0.8);
}
.basic-layout .am-navbar .am-navbar-left,
.basic-layout .am-navbar .am-navbar-right {
  color: #e1f5fe;
}
.basic-layout .am-navbar .am-navbar-title {
  color: #b3e5fc;
}
.basic-layout .am-list .am-list-header {
  color: #81d4fa;
}
.basic-layout .am-list-body {
  background-color: transparent;
}
.basic-layout .am-list-body .am-list-item {
  background-color: rgba(255, 255, 255, 0.8);
}
.basic-layout .am-list-body .am-list-item .am-input-label,
.basic-layout .am-list-body .am-list-item .am-list-content {
  color: #333;
}
.basic-layout .am-list-body .am-list-item .am-list-line::after {
  background-color: rgba(0, 0, 0, 0.1) !important;
}



